export default {
	data() {
		return {
			// 多行输入框输入的内容
			textVal: ''
		}
	},
	methods: {
		// 清除多行输入框内的内容
		remove() {
			this.textVal = ''
		},
		// 完成提交并返回
		submit() {
			uni.showModal({
				content: '提交成功',
				showCancel: false,
				confirmText: '返回',
				confirmColor: '#59b9a6',
				success: (res) => {
					if (res.confirm) {
						uni.switchTab({
							url: '/pages/me/me'
						});
					}
				}
			});
		}
	},
	computed: {
		isImg() {
			if (this.textVal == '') {
				return false
			}
			return true
		}
	},
}
